import React, {useState} from 'react';
import {Image, StyleSheet, Text, View} from 'react-native';
import {AuthContext} from '../../contexts/AuthContext';
import BasicButton from "../../components/buttons/BasicButton";
import HorizontalPicker from '@vseslav/react-native-horizontal-picker';
import StepNum from "../../components/StepNum";

// const TimeItems = Array.from(Array(10).keys());
const TimeItems = [
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    'data:image/png;base64,',
    'data:image/png;base64,',
    'data:image/png;base64,',
];


const List = (item, index) => (
    <View style={[styles.item, {width: 75}]}>
        <Image
            style={[styles.picture]}
            source={{
                uri: 'data:image/png;base64,',

            }}
        />
    </View>
);

export default function ProfileStep9Screen({route, navigation}) {
    const {logout} = React.useContext(AuthContext);
    const [picture, setPicture] = useState('');

    return (
        <View style={styles.container}>
            <View style={styles.header}>
                <View style={styles.step}>
                    <StepNum number={9}/>
                </View>
                <Text style={styles.text}>Profile picture</Text>
            </View>
            <View style={styles.content}>
                <View style={styles.contentTop}>
                    <HorizontalPicker
                        data={TimeItems}
                        renderItem={List}
                        itemWidth={75}
                        defaultIndex={1}
                        onChange={(position) => setPicture(TimeItems[position])}
                    />
                    <Image
                        style={[styles.triangle]}
                        source={{
                            uri:
                                " data:image/png;base64,"
                        }}
                    />
                    <Text style={[styles.months]}>Upload custom image</Text>
                </View>
                <View style={styles.contentMessage}>
                    <Text style={styles.message}>You can select any avatar you want form this list.{'\n'}
                        Or pick a custom image and show yourself to your buddies !
                    </Text>
                </View>
            </View>
            <View style={styles.buttons}>
                <BasicButton data={'plain'}
                             onButtonClick={'ProfileStep10Screen'}
                             ButtonText={'Continue'}
                             disabled={!(picture)}
                             params={Object.assign(route.params.params, {picture: picture})}/>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,

        alignItems: 'center',
        justifyContent: 'space-between',
        paddingLeft: 20,
        paddingRight: 20,
    },
    content: {
        flex: 1,
        justifyContent: 'center',
        alignSelf: 'stretch',
    },
    contentMessage: {
        marginTop: 50,
    },
    contentTop: {
        justifyContent: 'center',
    },
    message: {
        fontWeight: '600',
        fontSize: 16,
        marginTop: 30,
        textAlign: 'center',
    },
    buttons: {
        alignSelf: 'stretch',
        height: 115
    },
    text: {
        fontWeight: 'bold',
        fontSize: 14,
        marginTop: 30,
        textAlign: 'center',
    },

    buttonMesure: {
        flexDirection: 'row',
        justifyContent: 'center',
        marginTop: 30
    },
    button: {
        flexDirection: 'row',
        borderColor: '#34CC98',
        borderWidth: 1,
        width: 75,
        height: 50,
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: 25,
    },
    buttonText: {
        fontSize: 12,
        color: '#34CC98',
    },
    buttonClicked: {
        backgroundColor: '#34CC98',
    },
    textClicked: {
        color: 'white',
    },
    itemText: {
        fontSize: 14,
    },
    item: {
        marginTop: 50,
        textAlign: 'center',
    },
    triangle: {
        height: 11,
        width: 13,
        marginLeft: 'auto',
        marginRight: 'auto',
        marginTop: 10
    },
    months: {
        marginTop: 5,
        color: '#34CC98',
        fontWeight: 'bold',
        textAlign: 'center'
    },
    picture: {
        width: 75,
        height: 75,
    }
});