import * as React from 'react';
import {useEffect, useState} from 'react';
import {Image, Keyboard, StyleSheet, TouchableOpacity, useWindowDimensions, View} from 'react-native';
import LinearGrad from "../LinearGrad";
import {DrawerActions} from '@react-navigation/native';

export default function HeaderHome({navigation}) {
    const windowWidth = useWindowDimensions().width;
    const windowHeight = useWindowDimensions().height;
    const [keyboard, setKeyboard] = useState(false);

    useEffect(() => {
        Keyboard.addListener('keyboardDidShow', _keyboardDidShow);
        Keyboard.addListener('keyboardDidHide', _keyboardDidHide);

        // cleanup function
        return () => {
            Keyboard.removeListener('keyboardDidShow', _keyboardDidShow);
            Keyboard.removeListener('keyboardDidHide', _keyboardDidHide);
        };
    }, []);
    const _keyboardDidShow = () => {
        setKeyboard(true)
    };

    const _keyboardDidHide = () => {
        setKeyboard(false)
    };

    return (
        <View style={[styles.container, {width: windowWidth}]}>
            <View style={[styles.content]}>
                <TouchableOpacity
                    onPress={() => navigation.dispatch(DrawerActions.openDrawer())} style={styles.return}>
                    <Image
                        style={styles.picto}
                        source={{
                            uri:
                                " data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAABHNCSVQICAgIfAhkiAAAAG5JREFUSEvtk7ERwDAIA6WJkuy/hDMRqVJwxwUTGypcyxbSGyL5MPl9tIHbcG1FIjIAHO5Y34JB8nolKsEmg5vkaRosTm5er2WQnmATg4YcI1X7i35AVkCtbKubrLbWNYi1O6euZTA3U0zVCdy+Hl7tHhm5/PydAAAAAElFTkSuQmCC"
                        }}
                    />
                </TouchableOpacity>
                <LinearGrad/>
                <Image
                    style={[styles.logo, keyboard === true ? styles.keyboard : '']}
                    resizeMode="contain"
                    source={{
                        uri:
                            "data:image/png;base64,"
                    }}
                />
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {},
    content: {
        padding: 20,
        justifyContent: 'center',
        alignItems: 'center',
        borderBottomLeftRadius: 15,
        borderBottomRightRadius: 15,
        overflow: 'hidden',
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 6,
        },
        shadowOpacity: 0.16,
        shadowRadius: 10.00,
        elevation: 1,
    },
    keyboard: {
        height: 50,
    },
    title: {
        fontSize: 24,
        color: 'white',
        fontWeight: '600',
        textAlign: 'center',
        marginTop: 10
    },
    logo: {
        minWidth: 50,
        minHeight: 50,
        width: 150,
        height: 150,
    },
    picto: {
        width: 24,
        height: 24,
    },
    return: {
        position: 'absolute',
        left: 20,
        top: 13
    }
});